import React, {useEffect} from "react";
import {graphql} from "gatsby";
import Layout from "../../components/layout";
import Prism from 'prismjs';
import {formatDate} from "../../utils/date";
import 'prismjs/themes/prism-tomorrow.css';
import 'prismjs/components/prism-bash.min.js';
import 'prismjs/components/prism-typescript.min.js';
import 'prismjs/components/prism-javascript.min.js';
import 'prismjs/components/prism-json.min.js';
import 'prismjs/components/prism-python.min.js';
import {ReactCusdis} from "react-cusdis";
import {SignupForm} from "../../components/signup-form";


interface BlogPostProps {
    data: {
        hashnode: {
            publication: {
                post: {
                    id: string;
                    title: string;
                    url: string;
                    publishedAt: string;
                    coverImage: {
                        url: string;
                    };
                    content: {
                        html: string;
                    },
                    seo?: {
                        title?: string;
                    }
                }
            }
        }
    }
}

const Post = ({data}: BlogPostProps) => {
    const {id, title, url, publishedAt, content, seo} = data.hashnode.publication.post;

    useEffect(() => {
        Prism.highlightAll();
    }, [content]);

    return (
        <Layout>
            <title>{title}</title>
            <h1>{title}</h1>
            <p style={{marginTop: "-1rem"}}><small>{formatDate(publishedAt)}</small></p>
            { data.hashnode.publication.post.coverImage && <img src={data.hashnode.publication.post.coverImage.url} alt={title}/> }
            <br />
            <div dangerouslySetInnerHTML={{__html: content.html}}/>
            <br />
            <SignupForm />
            <br />
            {
                (seo?.title || id) && <ReactCusdis
                    attrs={{
                        host: 'https://cusdis.com',
                        appId: 'e9bc0bcb-d463-4f63-8604-34e78a1ec232',
                        pageId: seo?.title ? seo.title : id,
                        pageTitle: title,
                        pageUrl: url
                    }}
                />
            }
        </Layout>
    );
};

export const query = graphql`
    query BlogPostBySlug($slug: String!) {
        hashnode {
            publication(host: "xprilion.hashnode.dev") {
                isTeam
                title
                post(slug: $slug) {
                    id
                    title
                    url
                    coverImage {
                        url
                    }
                    content {
                        html
                    }
                    publishedAt
                    seo {
                        title
                    }
                }
            }
        }
    }
`;

export default Post;
